.top-slider
  position: relative
  overflow: hidden
  filter: drop-shadow(0px 4px 56px rgba(0, 0, 0, 0.51))
  user-select: none

  &__slide
    height: 350px

    +md-block
      height: 700px

  &__img
    width: 100%
    height: 100%
    object-fit: cover
    vertical-align: middle

  &__nav
    position: absolute
    left: 0
    bottom: 0
    display: flex
    z-index: 1

  &__button
    flex: 0 0 64px
    width: 64px
    height: 64px
    display: flex
    align-items: center
    justify-content: center
    background-color: #fff
    cursor: pointer

    svg
      fill: #979797
      width: 16px
      height: 16px

    &:hover
      background-color: #E4BD72

      svg
        fill: #000

  &__prev
    svg
      transform: rotate(-90deg)

  &__next
    svg
      transform: rotate(90deg)
