.consult
  position: relative
  padding-left: 52px
  max-width: 650px

  .modal &
    padding-left: 0

  &::before
    content: ""
    display: block
    width: 8px
    height: 170px
    background: radial-gradient(92.33% 50.91% at 50% 0%, rgba(250, 249, 245, 0.37) 0%, rgba(218, 174, 108, 0.37) 100%), #E0B588
    box-shadow: 0px 3px 22px rgba(223, 180, 128, 0.49)
    position: absolute
    top: 10px
    left: 0

    .modal &
      display: none

  &__header
    font-size: 24px
    text-transform: uppercase
    font-family: $font-header
    margin-bottom: 18px

    @include md-block
      font-size: 38px

    span
      color: #E9C878

  &__text
    font-size: 18px
    line-height: 1.23
    margin-bottom: 42px

    +lg-block
      font-size: 22px

    span
      color: #E9C878

  &__form
    display: flex
    flex-direction: column
    gap: 28px

    @include lg-block
      flex-direction: row
      align-items: flex-end

  &__submit
    width: 207px
    height: 55px
