*
  margin: 0
  padding: 0
  box-sizing: border-box
  -webkit-tap-highlight-color: transparent

html
  width: 100%
  height: 100%
  font-size: 16px
  scroll-behavior: smooth

body
  width: 100%
  min-height: 100%
  background-color: #fff
  color: $font-color
  font-family: $font-base
  min-width: 320px

a
  text-decoration: none
  color: $c-accent

img
  max-width: 100%
  height: auto

.container
  @include container()
  @include sm(max-width, rem(510))
  @include md(max-width, rem(690))
  @include lg(max-width, rem(930))
  @include xl(max-width, rem(1260))

.section
  position: relative
  padding: 70px 0
  &__description
    max-width: 922px
    margin: 0 auto
    text-align: center
    font-size: 17px
    line-height: 1.47
    position: relative
    margin-bottom: 30px
    z-index: 3
  &__over-title
    font-size: 16px
    position: relative
    border-radius: 12px
    background-color: #33333A
    font-family: $font-header
    text-align: center
    padding: 10px 20px
    position: relative
    margin: 0 auto
    transform: translate(0,-50%)
    width: 90%
    max-width: 1024px
    z-index: 3
    @include md-block
      font-size: 22px
    span
      color: #E6C59F
