.input
  $block: &
  position: relative

  &__input
    display: block
    height: 44px
    border: none
    border-bottom: 3px solid currentColor
    background: transparent
    width: 250px
    outline: none
    color: currentColor
    font-size: 18px
    padding-left: 32px
    font-weight: 700
    max-width: 100%

    &:invalid
      border-color: red

    &:focus ~ #{$block}__label,
    &.no-empty ~ #{$block}__label
      left: 0
      top: 0
      transform: translate(0,-100%)

  &__icon
    position: absolute
    left: 0
    top: 50%
    transform: translateY(-50%)
    pointer-events: none

  &__label
    position: absolute
    left: 0
    top: 50%
    transform: translate(32px, -50%)
    pointer-events: none
    transition: all .4s
