.modal
  position: fixed
  top: -100%
  left: 50%
  width: 992px
  padding: 14px
  max-width: 90%
  transform: translate(-50%, -50%)
  background: #fff
  border-radius: 18px
  z-index: 1000

  &.active
    top: 50%

  &__inner
    background: #fff
    padding: 30px
    box-shadow: 0px 0px 92px rgb(0 0 0 / 25%)
    border-radius: 18px
    position: relative

  &__close
    position: absolute
    width: 30px
    height: 30px
    display: flex
    align-items: center
    justify-content: center
    right: 10px
    top: 10px
    cursor: pointer

    &::before, &::after
      content: ""
      display: block
      width: 100%
      height: 3px
      background-color: #000
      border-radius: 3px
      position: absolute

    &::before
      transform: rotate(45deg)

    &::after
      transform: rotate(-45deg)
