.contact-box
  position: relative
  background-color: #fff
  border-radius: 18px
  padding: 14px
  width: 390px
  max-width: 90%

  a
    color: currentColor

  .icon
    fill: #E9C878

  &__inner
    border-radius: 18px
    background-color: #fff
    box-shadow: 0px 0px 92px rgba(0, 0, 0, 0.25)
    padding: 35px 24px
    text-align: center

  &__header
    font-family: $font-header
    text-transform: uppercase
    font-size: 26px
    margin-bottom: 23px

  &__content
    display: flex
    flex-direction: column
    gap: 22px

  &__label
    color: #929292
    margin-bottom: 10px

  &__info
    display: flex
    align-items: center
    justify-content: center
    flex-wrap: wrap
    gap: 15px
    text-align: center
