.officeCard
  $val: &
  display: flex
  flex-direction: column
  padding: 28px
  border-radius: 10px
  overflow: hidden
  height: 411px
  position: relative
  color: #fff
  transition: all .4s

  &.active
    box-shadow: 0px -9px 29px rgba(0, 0, 0, 0.25)

  &::before
    content: ""
    display: block
    width: 100%
    height: 100%
    background: linear-gradient(0deg, rgba(37, 37, 45, 0.87) 26.03%, rgba(37, 37, 45, 0.3045) 100%)
    position: absolute
    left: 0
    top: 0
    z-index: 1

  &__image
    position: absolute
    width: 100%
    height: 100%
    object-fit: cover
    left: 0
    top: 0
    z-index: 0
    vertical-align: middle

  &__header
    position: relative
    z-index: 2
    display: flex
    align-items: center
    justify-content: space-between

    &::before
      content: ""
      display: block
      height: 65px
      border-left: 1px solid #ffffff20
      margin: 0 10px

  &__footer
    position: relative
    z-index: 2
    margin-top: auto

  &__btn
    width: 100%
    position: relative
    z-index: 4

  &__more
    display: flex
    align-items: center
    column-gap: 14px
    margin-bottom: 13px
    cursor: pointer

    &::before, &::after
      content: ""
      display: block
      border-bottom: 1px solid #ffffff30
      flex: 1

    .icon
      display: inline-block
      vertical-align: middle
      fill: #fff
      width: 8px
      height: 5px
      margin-left: 5px

  &__logo
    max-width: 157px
    order: -1

  &__percent
    font-size: 15px
    strong
      font-size: 22px

  &__info
    display: flex
    align-items: flex-end
    justify-content: space-between
    column-gap: 20px
    font-size: 14px
    line-height: 1.29
    margin-bottom: 8px

  &__left
    flex: 1

  &__info-right
    flex: 1
    text-align: right

  &__name
    font-weight: 700

  &__modal
    background-color: #fff
    padding: 25px 18px 92px
    border-radius: 12px 12px 0 0
    position: absolute
    bottom: -36px
    left: -16px
    right: -16px
    z-index: 3
    color: #000
    opacity: 0
    pointer-events: none
    transition: all .4s

    #{$val}.active &
      opacity: 1
      bottom: -28px
      pointer-events: all
