.dealCard
  position: relative
  padding-left: 83px
  padding-top: 30px
  counter-increment: gridItem
  @include lg-block
    padding-left: 40px

  &::before
    content: counter(gridItem, decimal-leading-zero)
    position: absolute
    left: 43px
    transform: translateX(-50%)
    top: 0
    font-size: 22px
    +text-gradient(#DFB46D,#E0D5B8)
    @include lg-block
      left: 0

  &::after
    content: ""
    display: block
    position: absolute
    top: 30px
    left: 43px
    transform: translateX(-50%)
    width: 43px
    height: 43px
    background: url('../images/dealCard/deal-dot.svg') no-repeat center
    @include lg-block
      left: 0

  &__title
    display: flex
    align-items: center
    min-height: 43px
    margin-bottom: 10px
    position: relative

    &::before
      content: ""
      display: block
      position: absolute
      left: -40px
      top: 20px
      width: 0
      height: 64px
      border-left: 1px solid #E9C878

  &__text
    color: #B5B5C6
    font-size: 15px
    line-height: 1.4
