.btn
  display: inline-flex
  font-family: $font-base
  white-space: nowrap
  padding: 5px 20px
  height: 40px
  align-items: center
  justify-content: center
  text-align: center
  text-transform: uppercase
  background: radial-gradient(92.33% 50.91% at 50% 0%, rgba(250, 249, 245, 0.37) 0%, rgba(218, 174, 108, 0.37) 100%), #E0B588
  box-shadow: 0px 3px 22px rgba(223, 180, 128, 0.49), 0px 2px 0px #8D6A3E
  border-radius: 50px
  color: #55422F
  font-weight: 500
  font-size: 12px
  border: none
  outline: none
  cursor: pointer
  user-select: none

  &:hover
    background: radial-gradient(92.33% 50.91% at 50% 0%, rgba(218, 174, 108, 0.37) 0%, rgba(250, 249, 245, 0.37) 100%), #E0B588
