.header
  position: fixed
  width: 100%
  top: 0
  left: 0
  z-index: 20
  background-color: transparent
  padding: 33px 0
  color: #fff

  &__phones
    display: flex
    flex-direction: row
    gap: 12px
    margin-left: auto
    flex: 0 0 auto

    @media screen and ( min-width: 420px )
      flex-direction: column

    +md-block
      grid-area: 24px
      flex-direction: row

  &.fixed
    background-color: #25252D

  &__container
    display: flex
    align-items: center
    justify-content: space-between
    gap: 24px
    flex-wrap: wrap

    +sm-block
      flex-wrap: nowrap
