.invest
  position: relative
  padding: 47px 0 0
  max-width: 1920px
  margin: 0 auto

  &__cloud
    position: absolute
    z-index: 2
    width: 100%
    height: 274px
    background: url(../images/cloud.webp) repeat-x center

  &__image
    vertical-align: middle
    width: 100%
    height: 100%
    object-fit: cover
    max-width: 1920px
    max-height: 802px

  &__slider
    position: relative
    overflow: hidden

  &__slide
    position: relative

  &__slide-wrapper
    position: relative
    max-height: 802px

    &::before
      content: ""
      display: block
      width: 100%
      height: 100%
      position: absolute
      left: 0
      top: 0
      background: linear-gradient(180deg, #FFFFFF 7.86%, rgba(255, 255, 255, 0) 24.06%)
      z-index: 3

  &__point
    position: absolute
    display: none
    align-items: center
    justify-content: center
    width: 26px
    height: 26px
    border: 1px solid #E8CBAA
    border-radius: 50%

    @include md-block
      display: flex

    &::before
      content: ""
      display: block
      width: 14px
      height: 14px
      border-radius: 50%
      background: radial-gradient(92.33% 50.91% at 50% 0%, rgba(250, 249, 245, 0.37) 0%, rgba(218, 174, 108, 0.37) 100%), #E0B588
      box-shadow: 0px 3px 22px rgba(223, 180, 128, 0.49), 0px 1px 0px #8D6A3E

  &__point-text
    position: absolute
    width: 284px
    text-align: center
    color: #fff
    font-weight: 500
    bottom: 44px
    right: 0
    line-height: 1.2
    font-size: 15px
    background: rgba(17, 17, 25, 0.8)
    padding: 15px 10px
    border-radius: 8px

    span
      color: #E8CBAA

    &::before
      content: ""
      display: block
      width: 0
      height: 0
      position: absolute
      border: 8px solid transparent
      border-top-color: rgba(17, 17, 25, 0.8)
      top: 100%
      left: 50%
      transform: translateX(-50%)

    @include md-block
      right: auto

  &__content
    margin-top: 50px
    @include lg-block
      margin-top: 0
      position: absolute
      bottom: 12%
      z-index: 2

  &__pagination
    display: flex
    flex-direction: column
    flex-wrap: wrap
    width: 100%
    flex: 1
    background-color: #fff
    border-radius: 14px
    align-items: stretch
    position: relative
    z-index: 3

    &::before
      content: ""
      position: absolute
      display: block
      border-radius: 19px
      border: 1px dashed #E9C878
      top: -5px
      left: -5px
      right: -5px
      bottom: -5px
      z-index: 1

    @include lg-block
      width: 131px

    &-years
      display: flex

      @include lg-block
        flex-direction: column

    &-year
      display: flex
      align-items: center
      justify-content: center
      width: 100%
      height: 96px
      cursor: pointer
      font-size: 22px
      font-family: $font-header
      flex: 1
      position: relative
      z-index: 2

      @include lg-block
        flex: 0 0 96px

      &:first-child
        border-radius: 14px 0 0 14px
        @include lg-block
          border-radius: 0

      &:last-child
        border-radius: 0 14px 14px 0
        @include lg-block
          border-radius: 0 0 14px 14px

      &.swiper-pagination-bullet-active
        background-color: #F3F1EC
        span
          @include text-gradient(#EF8E35,#F49E28)

    &-header
      display: flex
      align-items: center
      justify-content: center
      padding: 11px
      text-align: center
      font-size: 14px
      font-weight: 500
      position: relative
      z-index: 2
      flex: 1 0 100%
      height: 60px

      @include lg-block
        height: 82px
        flex: 0 0 82px

  &__info
    margin: 20px 0

    @include lg-block
      position: absolute
      bottom: 12%
      margin: 0 0 0 176px
      z-index: 5

  &__info-slide
    display: flex
    gap: 15px

  &__info-block
    position: relative
    display: flex
    flex-direction: column
    align-items: center
    flex: 1
    border-radius: 19px
    padding: 25px 16px
    text-align: center

    @include lg-block
      text-align: left
      flex: 0 0 173px
      width: 173px
      align-items: flex-start

    .text
      font-size: 14px
      line-height: 1.14
      margin-bottom: 5px

    .num
      font-size: 30px
      font-family: $font-header
      white-space: nowrap

      @include sm-block
        font-size: 40px

    .subtext
      font-family: $font-header
      font-size: 21px
      line-height: 0.71
      text-align: right

      @include lg-block
        align-self: flex-end
        margin-right: 32px

    .link
      position: absolute
      width: 21px
      height: 21px
      display: flex
      align-items: center
      justify-content: center
      border-radius: 6px
      background-color: #75D5A5
      right: -5px
      top: -5px

      .icon
        width: 12px
        height: 12px
        fill: #fff

    &:nth-child(1)
      background-color: #E6C59F
      color: #55422F

    &:nth-child(2)
      background-color: #25252D
      color: #fff
