.sectionTitle
  line-height: 1.47
  font-family: $font-header
  font-weight: 400
  text-align: center
  text-transform: uppercase
  margin-bottom: 50px
  position: relative
  z-index: 3

  span
    color: #DFB46D

  &--no-margin
    margin-bottom: 0
  &--normal
    font-size: 24px
    @include md-block
      font-size: 36px
    @include lg-block
      font-size: 45px
  &--large
    font-size: 44px
    color: #DFB46D
    @include md-block
      font-size: 60px
    @include lg-block
      font-size: 82px

  #buisness &
    margin-bottom: 0
