.payback
  display: flex
  flex-direction: column
  align-items: center
  gap: 27px
  width: 100%
  text-align: center
  position: relative

  +md-block
    flex-direction: row
    width: 390px
    text-align: left

  &::before
    display: none
    content: ''
    position: absolute
    width: 121px
    height: 336px
    background: url('../images/payback/payback.webp') no-repeat center
    top: -115px
    left: -50px
    z-index: 0

    +lg-block
      display: block

  &__num
    +text-gradient(#DFB46D,#E0D5B8)
    font-family: $font-header
    font-size: 79px
    position: relative
    z-index: 1

  &__text
    font-size: 18px
    font-family: $font-header
    text-transform: uppercase
    z-index: 1
