.grid
  $block: &
  counter-reset: gridItem

  &.swiper-initialized
    overflow: hidden

  &:not(.swiper-initialized)
    #{$block}__wrapper
      display: grid
      grid-template-columns: repeat(1, 1fr)
      gap: 30px

      @include lg-block
        grid-template-columns: repeat(2, 1fr)

        @include xl-block
          grid-template-columns: repeat(3, 1fr)
