.footer
  background-color: #222228

  &__top
    padding: 43px 0
    color: #fff

  &__bottom
    background-color: #25252D
    padding: 25px 0
    color: #6B6B70
    font-size: 12px

    a
      color: currentColor

  &__container
    display: flex
    flex-wrap: wrap
    align-items: center
    justify-content: space-between
    gap: 30px

  &__contact
    display: flex
    flex-direction: column
    align-items: flex-end
    text-align: right
    gap: 10px
