.service
  position: relative

  &::before
    content: ""
    display: none
    width: 354px
    height: 1053px
    background: url('../images/service-tree.webp') no-repeat left top
    position: absolute
    left: 0
    top: 0
    z-index: 1

    +lg-block
      display: block

  &__top
    position: relative
    overflow: hidden

    &::after
      display: block
      content: ""
      height: 100%
      position: absolute
      width: 20vw
      background: linear-gradient(270deg, #25252D 15.94%, rgba(37, 37, 45, 0) 60.04%)
      top: 0
      right: 0
      z-index: 10

    &::before
      display: block
      content: ""
      height: 100%
      position: absolute
      width: 20vw
      background: linear-gradient(270deg, rgba(37, 37, 45, 0) 15.94%, #25252D 60.04%)
      top: 0
      left: 0
      z-index: 10

  &__nav
    position: relative
    top: 24px
    z-index: 11

  &__prev, &__next
    position: absolute
    display: flex
    align-items: center
    justify-content: center
    top: 50%
    transform: translateY(-50%)
    cursor: pointer
    width: 40px
    height: 40px

  &__prev
    left: -20px

    @media screen and (min-width: 1600px)
      left: -40px

  &__next
    right: -20px

    @media screen and (min-width: 1600px)
      right: -40px

  &__container
    position: relative

  .swiper-wrapper
    display: flex

  &__item
    display: flex
    align-items: center
    font-weight: 700
    font-size: 16px
    opacity: 0.6
    height: auto
    position: relative
    padding-bottom: 13px

    &.swiper-slide-active
      opacity: 1

      &::after
        position: absolute
        bottom: 0
        left: 0
        content: ""
        display: block
        width: 100%
        height: 3px
        max-width: 197px
        background: radial-gradient(92.33% 50.91% at 50% 0%, rgba(250, 249, 245, 0.37) 0%, rgba(218, 174, 108, 0.37) 100%), #E0B588
        box-shadow: 0px 3px 22px rgba(223, 180, 128, 0.49)

      .service__link
        transform: scale(1)
        color: #DFB46D

  &__link
    transform: scale(.8)
    color: #fff

  &__bottom
    padding: 100px 0 175px
    overflow: hidden
    background: url('../images/service-line.webp') no-repeat 400px bottom

    +lg-block
      background: url('../images/service-line.webp') no-repeat 400px bottom, url('../images/firstscreenImg.webp') no-repeat right bottom
      background-size: auto, 50%

    @media screen and ( min-width: 1600px )
      background-size: auto, auto

  &__slider
    counter-reset: service

  &__pagination
    display: flex
    gap: 20px
    margin-top: 30px
    position: relative
    z-index: 2

    .swiper-pagination-bullet
      display: flex
      align-items: center
      justify-content: center
      width: 12px
      height: 12px
      border-radius: 50%
      background: rgba(196, 196, 196, 0.29)
      cursor: pointer

    .swiper-pagination-bullet-active
      background: #DFB46D
      position: relative

      &::before
        position: absolute
        content: ""
        display: block
        width: 22px
        height: 22px
        background: url('../images/progress.svg') no-repeat center
        z-index: 10
        border-radius: 50%
        animation: linear rotate infinite 5s

  &__num
    display: flex
    align-items: center
    column-gap: 10px
    font-family: $font-header
    font-size: 55px
    +text-gradient(#DFB46D,#E0D5B8)
    line-height: 1
    margin-bottom: 24px

    &::after
      content: ""
      display: block
      width: 58px
      border-bottom: 1px solid #ffffff20

  &__content
    max-width: 574px

  &__title
    color: #E8CDA1
    margin-bottom: 22px
    line-height: 1.21
    font-size: 39px
    font-weight: 300

  &__info
    font-size: 19px
    line-height: 1.79

@keyframes rotate
  0%
    transform: rotate(0)
  100%
    transform: rotate(360deg)
