@use "sass:math";

.icon {
    display: inline-block;
    width: 1em;
    height: 1em;
    fill: currentColor;
}

.icon-address {
    width: math.div(14, 17) * 1em;
}
.icon-arrow {
    width: math.div(8, 5) * 1em;
}
.icon-arrow2 {
    width: math.div(11, 11) * 1em;
}
.icon-mail {
    width: math.div(16, 12) * 1em;
}
.icon-next {
    width: math.div(14, 24) * 1em;
}
.icon-phone {
    width: math.div(14, 14) * 1em;
}
.icon-prev {
    width: math.div(14, 24) * 1em;
}
.icon-recall {
    width: math.div(15, 15) * 1em;
}
.icon-user {
    width: math.div(16, 16) * 1em;
}
