.mainmenu
  display: flex
  flex-direction: column
  gap: 15px
  font-size: 11px
  font-weight: 500
  text-transform: uppercase
  line-height: 1.36

  &__item
    .header &
      margin-bottom: 15px

      +xl-block
        margin-bottom: 0

      br
        display: none
        +xl-block
          display: block

  &__link
    display: inline-flex
    position: relative
    color: currentColor

    br
      display: none

      @include sm-block
        display: block

    &::before
      content: ""
      display: block
      width: 17px
      height: 4px
      background: url('../images/mainmenu/tilda.svg') no-repeat center
      position: absolute
      left: 0
      bottom: -10px
      opacity: 0
      pointer-events: none
      transition: all .4s

    &:hover
      color: #E7C9A6

      &::before
        opacity: 1

  .header &
    display: none
    position: fixed
    left: -320px
    top: 0
    opacity: 0
    transition: all .2s ease

    &.active
      display: flex
      flex-direction: column
      background: #25252D
      left: 0
      width: 320px
      max-width: 90%
      height: 100vh
      z-index: 110
      padding: 30px
      border-right: 1px solid #DFB46D
      opacity: 1

    +xl-block
      opacity: 1
      position: static
      display: flex
      flex-direction: row
      flex: 1
      justify-content: space-around

  .footer &
    flex-direction: column
    flex: 1 0 100%
    order: 1

    @include sm-block
      flex-direction: row
      align-items: baseline
      justify-content: space-between

    @include lg-block
      flex: 1
      order: 0
      justify-content: space-around
      margin: 0 50px
