.parametr
  display: flex
  justify-content: space-around
  flex-wrap: wrap
  gap: 30px 20px

  @include md-block
    gap: 30px

  @include xl-block
    justify-content: space-between

  &__item
    flex: 1 0 calc(50% - 10px)

    @include md-block
      flex: 0 0 auto

  &__name
    font-weight: 500
    font-size: 15px
    margin-bottom: 20px

    @include md-block
      font-size: 17px

  &__value
    font-family: $font-header
    font-size: 40px
    line-height: 1
    @include text-gradient(#DFB46D,#E9C878)
    @include md-block
      font-size: 60px
    @include xl-block
      font-size: 110px

    span
      font-size: calc(69/110) + em
