.gallery
  $block: &
  display: grid
  counter-reset: gallery
  margin-bottom: 50px
  grid-template-columns: repeat(2, 1fr)

  @include lg-block
    grid-template-columns: 1fr 36.585% 1fr
    grid-template-rows: 1fr 1fr

  &__item
    position: relative
    height: 180px
    counter-increment: gallery
    overflow: hidden

    &:hover
      #{$block}__image
        transform: scale(1.1)

    @include sm-block
      height: 302px

    &::before
      content: ""
      display: block
      width: 100%
      height: 100%
      position: absolute
      top: 0
      left: 0
      background: linear-gradient(360deg, rgba(6, 13, 23, 0.7) 12.66%, rgba(6, 13, 23, 0) 100%)
      z-index: 1

    &:first-child
      grid-column: span 2

      @include lg-block
        grid-column: span 1
        grid-row: span 2
        height: auto

  &__image
    width: 100%
    height: 100%
    object-fit: cover
    transition: all .6s ease

  &__name
    position: absolute
    color: #fff
    font-size: 16px
    left: 15px
    bottom: 15px
    z-index: 1

    @include sm-block
      left: 37px
      bottom: 37px

    @include md-block
      font-size: 23px

  &__num
    display: flex
    align-items: center
    justify-content: center
    position: absolute
    left: 15px
    font-size: 22px
    top: 15px
    color: #fff
    gap: 13px
    opacity: .7
    transition: opacity .4s
    z-index: 1

    @include sm-block
      top: auto
      writing-mode: vertical-lr
      left: 37px
      bottom: 128px
      flex-direction: row-reverse

    .gallery__item:hover &
      opacity: 1

    &::before
      content: counter(gallery, decimal-leading-zero)
      @include sm-block
        transform: rotate(180deg)

    &::after
      content: ''
      display: block
      width: 80px
      height: 0
      border-bottom: 1px solid #fff
      @include sm-block
        width: 0
        height: 80px
        border-bottom: none
        border-left: 1px solid #fff
