.artCard
  $var: &
  position: relative
  display: flex
  flex-direction: column
  justify-content: flex-end
  background-color: #F5E9DB
  border-radius: 9px
  padding: 40px 30px 20px
  height: 311px
  color: #fff
  //transition: all .4s

  &::before
    content: ""
    display: block
    width: 63px
    height: 48px
    background: url('../images/artCard/artIcon.svg') no-repeat center
    position: absolute
    z-index: 1
    top: -15px
    left: 32px
    transform: scale(0)
    transition: all .2s

  &::after
    content: ""
    display: block
    position: absolute
    left: 0
    top: 0
    width: 100%
    height: 100%
    background: linear-gradient(0deg, #25252D 0%, rgba(37, 37, 45, 0.28) 47.59%), linear-gradient(0deg, rgba(37, 37, 45, 0.33), rgba(37, 37, 45, 0.33))
    border-radius: 8px
    z-index: 0
    pointer-events: none

  //artCard__image
  &__image
    position: absolute
    width: 100%
    height: 100%
    top: 0
    left: 0
    border-radius: 9px
    object-fit: cover

  //artCard__content
  &__content
    position: relative
    z-index: 1
    max-height: 100%
    overflow: hidden
    transition: all .4s

  //artCard__title
  &__title
    font-size: 22px
    font-family: $font-header
    font-weight: 400
    margin-bottom: 10px

    @include sm-block
      font-size: 24px

    @include md-block
      font-size: 31px

  //artCard__text
  &__text
    font-size: 14px
    line-height: 1.64
    overflow: hidden
    text-overflow: ellipsis
    display: -moz-box
    -moz-box-orient: vertical
    display: -webkit-box
    -webkit-line-clamp: 2
    -webkit-box-orient: vertical
    line-clamp: 2
    box-orient: vertical

    span, a
      text-decoration: none
      color: #F2D3B1

  &:hover
    justify-content: flex-start

    &::after
      background: #F5E9DB

    &::before
      transform: scale(1)

    #{$var}__content
      color: #35393E

    #{$var}__text
      display: block

      span, a
        color: #AF732C
