.top-content
  max-width: 565px
  margin-bottom: 74px
  &__title
    @extend .sectionTitle
    @extend .sectionTitle--normal
    text-align: left
    margin-bottom: 23px

  &__text
    display: flex
    flex-direction: column
    font-size: 17px
    line-height: 1.47

    &::after
      content: ""
      display: block
      width: 190px
      height: 8px
      background: radial-gradient(92.33% 50.91% at 50% 0%, rgba(250, 249, 245, 0.37) 0%, rgba(218, 174, 108, 0.37) 100%), #E0B588
      box-shadow: 0px 3px 22px rgba(223, 180, 128, 0.49)
      margin-top: 20px
