.top-info
  display: flex
  flex-direction: column
  gap: 43px

  &__title
    font-size: 39px
    line-height: 1.21
    color: #E8CDA1
    font-weight: 300

  &__text
    font-size: 17px
    line-height: 1.47

    p
      margin: 1em 0

  &__icons
    display: flex
    flex-direction: column
    gap: 30px
    justify-content: space-between

    +sm-block
      flex-direction: row

  &__icon-item
    display: flex
    gap: 10px
    align-items: center

  &__icon
    flex: 0 0 auto

  &__text
    flex: 1
    font-size: 17px
    line-height: 1.47

.top-stat
  display: flex
  justify-content: space-between
  gap: 30px
  padding-bottom: 43px
  border-bottom: 1px solid #C4C4C420

  &__item
    display: flex
    flex-direction: column

    .name
      font-size: 14px
      line-height: 1.47
      display: flex
      align-items: center
      min-height: 40px

      +md-block
        font-size: 17px

    .value
      margin-top: auto
      font-size: 32px
      +text-gradient(#DFB46D,#E9C878)
      font-family: $font-header

      +md-block
        font-size: 57px

      span
        font-size: 16px

        +md-block
          font-size: 32px
