.timer
  display: flex
  align-items: center
  justify-content: center
  font-family: $font-header
  gap: 12px

  &__icon
    flex: 0 0 67px
    width: 67px
    height: 67px

  &__num
    font-size: 79px
    color: #DFB46D

  &__text
    color: #DFB46D
    line-height: .8
    p
      font-size: 54px

      &:first-child
        color: #fff
        font-size: 30px
