/**
 * Bootstrap
 */
@import './variables'
/**
 * Vendor styles
 */
@import '../../vendor/vendor'
/**
 * Functions
 */
@import '../../base/sass/functions'

/**
 * Mixins
 */
@import '../../mixins/sass/_smart-grid'
@import '../../mixins/sass/sprite-img'
@import '../../mixins/sass/sprite-svg'
@import '../../mixins/sass/pos-center'
@import '../../mixins/sass/pos-strech'
@import '../../mixins/sass/responsive-title'
@import '../../mixins/sass/letter-spacing'
@import '../../mixins/sass/gradient'
@import '../../mixins/sass/font-face'
@import '../../mixins/sass/_text-gradient'

/**
 * Base
 */
@import './fonts'
@import './base'
@import './typography'
@import './png-icons'

/**
 * Components
 */
@import '../../components/components'

#art
  position: relative
  background: url('../images/tree1.webp') no-repeat left center, url('../images/list1.webp') no-repeat right 80px center

#deals
  background: url('../images/deal-tree.webp') no-repeat left top, url('../images/deal-leaf.webp') no-repeat right 70%, url('../images/deal-line.webp') no-repeat center top, #25252D
  color: #fff
  padding-bottom: 80px
  padding-top: 0
  .grid
    margin-bottom: 80px

#consult
  background-color: #222228
  padding: 60px 0 100px
  color: #fff

  &::before
    position: absolute
    content: ""
    width: 100%
    height: 524px
    bottom: 0
    left: 0

    @include xl-block
      background: url('../images/conphone.webp') no-repeat right -300px bottom -10px, url('../images/leaf3.webp') no-repeat left top

    @media screen and (min-width: 1600px)
      background: url('../images/conphone.webp') no-repeat left 1140px bottom -10px, url('../images/leaf3.webp') no-repeat left top

#invest, #map
  padding: 0

#buisness
  background-color: #FAF9F5

#first-screen
  background: linear-gradient(180deg, rgba(13, 13, 18, 0.17) 400px, #24242C 800px, #25252D 100%), url('../images/firstscreen.webp') no-repeat center top, #25252D
  padding-top: 215px
  color: #fff
  padding-bottom: 0

  .section__container
    display: flex
    flex-direction: column
    align-items: flex-start
    justify-content: space-between
    gap: 30px
    margin-bottom: 150px

    +lg-block
      flex-direction: row

#second-screen
  padding-top: 215px
  color: #fff
  padding-bottom: 0
  position: relative
  background: #2C2B30 url('../images/index2.png') no-repeat center top
  padding-bottom: 108px

  .section__container
    display: flex
    flex-direction: column
    align-items: flex-start
    justify-content: space-between
    gap: 30px
    margin-bottom: 150px

.top-wrapper
  display: flex
  flex-direction: column
  gap: 40px

  +xl-block
    flex-direction: row

  .top-slider,
  .top-info
    flex: 1
